.smart-filter {
  margin-bottom: 20px;
  min-width: 220px;
  width: 100%;


  .bx-tag-link {
    color: var(--theme-color-light);
    text-decoration: none;
    text-shadow: none;
    font-size: 12px;
  }

  span {
    &.bx-spm {
      display: inline-block;
      margin: 0 10px;
      height: 23px;
      color: #000;
      vertical-align: middle;
      font-size: 18px;
      line-height: 23px;
    }
  }

  // .smart-filter-section
  &-section{
    position: relative;

  }

  // .smart-filter-title
  &-title{
    font-size: em(20);
    font-weight: 500;
    color: var(--black);
    padding: 0 em(10);
    padding-bottom: em(8);
    border-bottom: 1px solid var(--gray-10);
  }

  // .smart-filter-parameters
  &-parameters{

    // .smart-filter-parameters-box
    &-box{
      position: relative;
      border-bottom: 1px solid var(--gray-10);

      &.bx-active {
        .smart-filter-block {
          i {
            &.bx-ft-sub {
              display: block;
              padding-bottom: 5px;
              font-style: normal;
              font-size: 14px;
            }
          }
        }
      }

      // .smart-filter-parameters-box-title
      &-title{
        position: relative;
        display: flex;
        align-items: center;
        padding: em(20) em(10);


        // .smart-filter-parameters-box-title-text
        &-text{
          //color: var(--brand-500);
          cursor: pointer;
          transition: 250ms linear all;
        }
      }
    }
  }
}









