      display: block;
      vertical-align: middle;

      &:first-child {
        margin-bottom: 5px;
        padding-bottom: 5px;
        border-bottom: 1px solid #e5e5e5;
      }

      label {
        display: flex;
        align-items: center;
        overflow: hidden;
        width: 100%;
        color: #575757;
        white-space: nowrap;
        cursor: pointer;

        &.disabled {
          background: transparent;
          color: #b8b8b8;
        }
