    -moz-border-image: url(images/result_popup_right.png) 4 4 4 20 repeat;
    -o-border-image: url(images/result_popup_right.png) 4 4 4 20 repeat;
    border-image: url(images/result_popup_right.png) 4 4 4 20 fill repeat;
