.drop-down-box {
  display: flex;
  flex-direction: column;
  row-gap: var(--gap);
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
}
.drop-down-box__content {
  min-height: 0;
  max-height: 58px;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}
.drop-down-box__content p {
  margin: 0;
}
.drop-down-box__content p:not(:last-child) {
  margin-bottom: 1em;
}
.drop-down-box__divider {
  margin: 0;
  border: 0;
}

.image-switch {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  display: flex;
}
.image-switch__item {
  flex-grow: 1;
  cursor: pointer;
  transition: var(--animation);
}
.image-switch__item:first-child .image-switch__img {
  opacity: 1;
  z-index: -1;
}
.image-switch__item:hover .image-switch__img {
  opacity: 1;
  z-index: -1;
}
.image-switch__img {
  position: absolute;
  left: 50%;
  top: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  transform: translateX(-50%);
  pointer-events: none;
  background-color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
}
.image-switch__img picture {
  height: 100%;
}
.image-switch__img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.image-pagination {
  position: absolute;
  z-index: 30;
  left: 0;
  bottom: 5px;
  width: 100%;
  display: flex;
  justify-content: center;
  transition: var(--animation);
}
.image-pagination__item {
  display: block;
  width: 0.25em;
  height: 0.25em;
  border-radius: 0.125em;
  margin: 0 3px;
  background-color: var(--gray-20);
  transition: var(--animation);
}
.image-pagination__item--active {
  width: 1em;
  transition: var(--animation);
  background-color: var(--brand-500);
}

