  left: -1px;
  top: 50%;
  width: 10px;
  height: 50%;
  border-radius: 2px;
  background-color: var(--brand-500);
  z-index: 2;
  transition: opacity 0.3s ease;
  border-radius: 0 50% 50% 0;
  width: 3px;
  transform: translateY(-50%);
}
.side-menu li:not(:first-of-type) {
  border-top: 1px solid var(--gray-10);
}
.side-menu .link-wrapper {
  position: relative;
  display: block;
}

.aside-block:not(:last-child) {
  margin-bottom: 20px;
}
.aside-banner img {
  border-radius: var(--border-radius);
  width: 100%;
  height: auto;
  overflow: hidden;
}
.aside-menu {
  margin-bottom: 20px;
}
.aside-menu__head {
  padding: 17px 23px 18px;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
  font-weight: 700;
  margin-bottom: -1px;
  border: 1px solid #e5e5e5;
  border-radius: 4px;
}
.aside-menu__head .svg-inline-down {
  position: absolute;
  right: 15px;
  top: 27px;
  transition: transform 0.2s;
  width: 20px;
  height: 20px;
  margin: -10px 0 0;
  text-align: center;
  border-radius: 3px;
  line-height: 16px;
  transition: all 0.1s ease;
  rotate: 180deg;
}
.aside-menu__head.toggle {
  border-radius: 4px 4px 0px 0px;
}
.aside-menu__head.toggle .svg-inline-down {
  rotate: 0deg;
}
.aside-menu__body {
  overflow: hidden;
  transition: all 0.3s ease;
}
.aside-menu__body.open {
  max-height: 100%;
}
.aside-menu__body.open .side-menu {
  border-radius: 0 0 var(--border-radius) var(--border-radius);
}

.side-menu .submenu-wrapper {
  user-select: none;
  display: none;
}
.side-menu li.opened.child > .submenu-wrapper {
  display: block;
}
.side-menu li.active .submenu a:before {
  background-color: transparent;
}
.side-menu .submenu {
  padding: 0 0 15px 22px;
  list-style: none;
  font-size: 14px;
}
.side-menu .submenu .menu-arrow.svg {
  top: 0;
}
.side-menu .submenu .submenu {
  padding: 9px 0;
}
.side-menu .submenu .submenu-wrapper {
  padding: 0 0 0 10px;
  display: none;
}
.menu-arrow.svg {
  position: absolute;
  right: 18px;
  top: 21px;
  width: 15px;
  height: 14px;
  text-align: center;
  line-height: 8px;
  border-radius: 3px;
  margin: 0;
  z-index: 23;
  transition: all 0.3s ease;
}
.menu-arrow.svg:hover {
  background-color: var(--brand-500);
}
.menu-arrow.svg:hover svg {
  fill: var(--white);
}
.menu-arrow.svg.toggle svg {
  rotate: 180deg;
}

.side-menu .submenu li {
  border: none;
}

.sublink {
  padding-block: 5px;
  padding-inline: 0 22px;
}

.toggle_block {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 20;
  width: 50px;
  height: 100%;
  bottom: 0;
  cursor: pointer;
}

.hero-banners-wrapper {
  width: 100%;
}
.hero-banners-wrapper .slick-slide {
  margin: 0;
}
.hero-banners-wrapper .slick-prev {
  left: 20px;
}
.hero-banners-wrapper .slick-next {
  right: 20px;
}
.hero-banners-wrapper.--small-left {
  display: flex;
  gap: 0.5em;
  height: 600px;
}
.hero-banners-wrapper.--small-left .slick-next,
.hero-banners-wrapper.--small-left .slick-prev {
  top: auto;
  bottom: 1.25em;
}
.hero-banners-wrapper.--small-left .slick-prev {
  left: inherit;
  right: 65px;
}
.hero-banners-wrapper.--small-left .slick-next {
  right: 20px;
}
.hero-banners-wrapper.--small-left .hero-banners-small {
  width: 400px;
}
.hero-banners-big {
  flex: 1;
  min-width: 0;
}
.hero-banners-item {
  height: 600px;
  position: relative;
}
.hero-banners-images {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.hero-banners-images video {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.hero-banners-inner {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}
.hero-banners-inner.--black {
  color: var(--black);
}
.hero-banners-inner.--white {
  color: var(--white);
}
.hero-banners-content {
  max-width: 600px;
  color: inherit;
}
.hero-banners-title {
  color: inherit;
}
.hero-banners-text {
  color: inherit;
}
.hero-banners-small-item {
  position: relative;
  height: 300px;
  overflow: hidden;
}
.hero-banners-small-item[onclick] {
  cursor: pointer;
}
.hero-banners-small-item:hover .hero-banners-small-images img {
  scale: 1.1;
}
.hero-banners-small-images {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.hero-banners-small-images img {
  transition: var(--animation);
}
.hero-banners-small-inner {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 2;
}
.hero-banners-small-inner.--black {
  color: var(--black);
}
.hero-banners-small-inner.--white {
  color: var(--white);
}
.hero-banners-small-label {
  color: inherit;
  opacity: 0.6;
}
.hero-banners-small-title {
  color: inherit;
}

.license-list-inner__section-content:not(:last-child) {
  margin-bottom: 5em;
}

.page-404 {
  padding: 50px 0;
}
.page-404__images .svg {
  width: 32.5em;
  height: 12.5em;
}
.page-404__images .svg svg {
  color: var(--brand-500);
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 767px) {
  .page-404__images .svg {
    width: 350px;
    height: 120px;
  }
}
@media screen and (max-width: 575px) {
  .page-404__images .svg {
    width: 290px;
    height: 104px;
  }
}
.page-404__title {
  font-size: 2.625em;
  line-height: 1.2;
}
@media screen and (max-width: 767px) {
  .page-404__title {
    font-size: 2em;
  }
}
@media screen and (max-width: 575px) {
  .page-404__title {
    font-size: 1.75em;
  }
}

:root {
  --m-font-family: "Manrope", system-ui, sans-serif;
  --m-font-family-title: "Manrope", system-ui, sans-serif;
  --bg-gray: #F4F5F7;
  --bg-white: #FFFFFF;
  --txt-black: #000000;
  --txt-dark-gray: #707070;
  --txt-gray: #A2A2A2;
  --txt-white: #FFFFFF;
  --black: #000;
  --light-gray: #F6F6F6;
  --green: #579E15;
  --beige: #E9E7E6;
  --white: #FFFFFF;
  --gap: 20px;
  --animation: all 0.25s ease-out;
}

main.main {
  flex-grow: 1;
  margin-bottom: 4.375em;
}
@media screen and (max-width: 575px) {
  main.main {
    margin-bottom: 2.5em;
  }
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  overflow-x: hidden;
  color: var(--txt-black);
  font-family: var(--m-font-family), serif;
  font-weight: 400;
  font-size: 1em;
  line-height: 1.5;
  min-height: 100vh;
  transition: background-color 0.3s ease;
  background-color: var(--bg-gray);
}

.flex1 {
  flex: 1;
}

.hero {
  background-size: cover;
  background-position: center;
  position: relative;
}
.hero:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.hero-inner {
  display: grid;
  grid-template-columns: 1fr 380px;
  gap: 60px;
  padding-top: 60px;
  padding-bottom: 40px;
  position: relative;
  z-index: 2;
}
.hero-title {
  color: var(--txt-white);
  font-size: 80px;
  font-weight: 800;
  line-height: 1;
  margin-bottom: 80px;
}
.hero-title span {
  display: block;
  font-size: 32px;
  font-weight: 700;
  margin-top: 20px;
  color: var(--green);
}
.hero-utp {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.hero-utp__wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  max-width: 90%;
}
.hero-utp-icons .svg {
  color: var(--white);
}
.hero-utp-title {
  color: var(--txt-white);
  text-align: center;
  font-size: 18px;
  line-height: 1.2;
  max-width: 90%;
}

.form {
  border-radius: 1.5625em;
  background: var(--white);
  overflow: hidden;
}
.form-top {
  text-align: center;
  background-color: var(--green);
  padding: 24px 8px;
}
.form-title {
  font-size: 18px;
  line-height: 1;
  font-weight: 600;
  color: var(--txt-white);
  text-transform: uppercase;
}
.form-description {
  margin: 0;
  color: var(--txt-white);
  font-size: 18px;
}
.form-inner {
  padding: 30px;
}
.form-inner label {
  font-size: 14px;
  color: var(--txt-dark-gray);
  line-height: 1.2;
  margin-bottom: 8px;
}
.form-inner-item:not(:last-child) {
  margin-bottom: 20px;
}
.form-inner .license-block {
  display: inline-flex;
  gap: 4px;
}
.form-inner .license-block input[type=checkbox] {
  flex: none;
}
.form-inner .license-block label {
  flex: 1;
  line-height: 1;
  font-size: 14px;
}
.form-inner .license-block label a {
  font-weight: 600;
  text-decoration: underline;
}
.form-inner .license-block label a:hover {
  color: var(--green);
}

.assortment {
  cursor: pointer;
  background-color: var(--beige);
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  height: 320px;
  transition: var(--animation);
}
.assortment:hover {
  background: var(--green);
}
.assortment-name {
  position: absolute;
  top: 20px;
  left: 20px;
  display: inline-flex;
  padding: 8px 20px;
  background-color: var(--white);
  border-radius: 30px;
  z-index: 3;
  color: var(--txt-black);
  font-size: 20px;
  font-weight: 600;
}
.assortment-images {
  position: absolute;
  bottom: -10%;
  right: -10%;
  max-width: 450px;
}
.assortment-images picture {
  display: block;
  width: 100%;
  height: 100%;
  line-height: 1;
}
.assortment-images img {
  max-width: 100%;
  height: auto;
}

.product-item {
  height: 360px;
  display: inline-flex;
  border-radius: 20px;
  overflow: hidden;
  border: 1px solid #e4e4e6;
}
.product-img {
  width: 360px;
}
.product-img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.product-inner {
  flex: 1;
  padding: 40px;
  background-color: var(--white);
  display: flex;
  flex-direction: column;
}
.product-top {
  display: flex;
  gap: var(--gap);
}
.product-content {
  flex: 1;
}
.product-title {
  color: var(--txt-black);
  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
  margin-bottom: 8px;
}
.product-text {
  color: var(--txt-dark-gray);
}
.product-text p {
  margin: 0;
}
.product-text p:not(:last-child) {
  margin-bottom: 1em;
}
.product-colors {
  width: 200px;
}
.product-bottom {
  margin-top: auto;
  display: flex;
  align-items: center;
  gap: 4px;
}
.product-price {
  color: var(--txt-black);
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
  flex: 1;
  opacity: 0.8;
}
.product-price span {
  opacity: 1;
  font-size: 30px;
  font-weight: 600;
}

.delivery {
  padding: 40px;
  background-color: var(--white);
  border-radius: 20px;
  border: 1px solid #e4e4e6;
}
.delivery-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}
.delivery-title {
  font-size: 32px;
  margin-bottom: 20px;
}
.delivery-list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 80%;
}
.delivery-list li {
  display: inline-flex;
  font-size: 20px;
  line-height: 1.2;
}
.delivery-list li:not(:last-child) {
  margin-bottom: 12px;
}
.delivery-list li:before {
  content: "";
  display: inline-flex;
  align-items: center;
  width: 24px;
  height: 24px;
  background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" viewBox="0 0 25 25" fill="none"><path d="M12.5 1.5625C10.3368 1.5625 8.22212 2.20397 6.42346 3.4058C4.62479 4.60763 3.22291 6.31583 2.39507 8.3144C1.56724 10.313 1.35064 12.5121 1.77267 14.6338C2.19469 16.7555 3.23639 18.7043 4.76602 20.234C6.29566 21.7636 8.24454 22.8053 10.3662 23.2273C12.4879 23.6494 14.687 23.4328 16.6856 22.6049C18.6842 21.7771 20.3924 20.3752 21.5942 18.5765C22.796 16.7779 23.4375 14.6632 23.4375 12.5C23.4375 9.59919 22.2852 6.8172 20.234 4.76602C18.1828 2.71484 15.4008 1.5625 12.5 1.5625ZM10.9375 16.8672L7.03125 12.9609L8.27344 11.7188L10.9375 14.3828L16.7266 8.59375L17.9734 9.83281L10.9375 16.8672Z" fill="%23579E15"/></svg>');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  flex: none;
  margin-right: 8px;
  top: 1px;
  position: relative;
}
.delivery-text {
  margin-top: 40px;
  font-size: 20px;
}

.about {
  padding: 40px;
  background-color: #E9E7E6;
  border-radius: 20px;
  border: 1px solid #e4e4e6;
  display: grid;
  grid-template-columns: 1fr 400px;
  gap: 40px;
}
.about-title {
  margin-bottom: 40px;
}
.about-inner-top {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-bottom: 20px;
}
.about-item {
  padding: 20px;
  background-color: var(--white);
  border-radius: 20px;
  text-align: center;
  font-size: 22px;
}
.about-item:last-child {
  grid-column: 1/-1;
}
.about-images {
  background-image: url("/assets/img/car.png");
  background-size: 145%;
  background-repeat: no-repeat;
  margin: -100px;
  margin-right: -40px;
  margin-left: 0;
  position: relative;
  top: 60px;
}

.payment-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.payment-item {
  padding: 40px 30px;
  background-color: var(--white);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  border: 1px solid #e4e4e6;
}
.payment-icons {
  width: 120px;
  height: 120px;
  background-color: var(--bg-gray);
  padding: 26px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.payment-icons .svg {
  max-width: 100%;
  height: auto;
}
.payment-title {
  text-align: center;
  font-size: 28px;
  font-weight: 500;
  line-height: 1;
}
.payment-text {
  text-align: center;
  color: var(--txt-dark-gray);
  max-width: 80%;
}

.contacts-wrapper {
  display: flex;
}
.contacts-wrapper .maps {
  flex: 1;
  height: 400px;
}
.contacts-inner {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  row-gap: 40px;
}
.contacts-item:last-child {
  grid-column: 1/-1;
}
.contacts-label {
  color: var(--txt-dark-gray);
  text-transform: uppercase;
  font-size: 16px;
  margin-bottom: 4px;
}
.contacts-value {
  font-size: 28px;
  line-height: 1.2;
  font-weight: 300;
}
.contacts-value a {
  display: block;
}
.contacts-value .work-schedule {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.contacts-value .work-schedule-item {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  gap: 4px;
}
.contacts-value .work-schedule-name {
  font-size: 24px;
}
.contacts-value .work-schedule-value {
  color: var(--txt-dark-gray);
  display: block;
}

.footer {
  background-color: var(--beige);
  padding-top: 20px;
  padding-bottom: 20px;
}
.footer-bottom {
  text-align: center;
  color: var(--txt-dark-gray);
  font-size: 12px;
}
.footer-bottom ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer-bottom ul li {
  display: inline-flex;
}

.open-form {
  padding: 40px;
  border-radius: 20px;
  background-size: cover;
  background-position: bottom;
  position: relative;
  overflow: hidden;
}
.open-form:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.open-form-wrapper {
  position: relative;
  z-index: 2;
}
.open-form-title {
  text-align: center;
  font-size: 24px;
  font-weight: 500;
  color: var(--white);
  text-transform: uppercase;
  margin-bottom: 40px;
}
.open-form-inner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  max-width: 900px;
  margin: 0 auto;
}
.open-form-inner label {
  color: var(--white);
  opacity: 0.8;
  margin-bottom: 4px;
}
.open-form-bottom {
  max-width: 900px;
  margin: 0 auto;
}
.open-form-bottom label {
  color: var(--white);
  opacity: 0.8;
}
.open-form-bottom > div {
  width: 100%;
  display: inline-flex;
  justify-content: center;
}